'use client';
import React, { useState } from 'react';

export function SettingsPage() {
  const [storeName, setStoreName] = useState('My Store');
  const [storeEmail, setStoreEmail] = useState('store@example.com');
  const [taxRate, setTaxRate] = useState(5);
  const [shippingMethod, setShippingMethod] = useState('Standard');

  const handleSubmit = (e: React.FormEvent, section: string) => {
    e.preventDefault();
    alert(`${section} settings saved successfully!`);
  };

  const handleReset = (section: string) => {
    if (section === 'Store') {
      setStoreName('My Store');
      setStoreEmail('store@example.com');
    } else if (section === 'Tax') {
      setTaxRate(5);
    } else if (section === 'Shipping') {
      setShippingMethod('Standard');
    }
    alert(`${section} settings reset to default.`);
  };

  return (
    <section className="p-6 space-y-8 bg-gray-50 rounded-lg shadow-lg">
      <h1 className="text-3xl font-semibold text-gray-800 mb-6">Settings</h1>

      {/* Store Settings */}
      <div className="p-6 bg-white rounded-lg shadow-md">
        <h2 className="text-2xl font-semibold text-gray-800 mb-4">Store Settings</h2>
        <form onSubmit={(e) => handleSubmit(e, 'Store')} className="space-y-4">
          <div>
            <label htmlFor="storeName" className="block text-gray-700 font-medium">
              Store Name
            </label>
            <input
              id="storeName"
              type="text"
              value={storeName}
              onChange={(e) => setStoreName(e.target.value)}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            />
          </div>
          <div>
            <label htmlFor="storeEmail" className="block text-gray-700 font-medium">
              Store Email
            </label>
            <input
              id="storeEmail"
              type="email"
              value={storeEmail}
              onChange={(e) => setStoreEmail(e.target.value)}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            />
          </div>
          <div className="flex justify-between">
            <button
              type="submit"
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Save Settings
            </button>
            <button
              type="button"
              onClick={() => handleReset('Store')}
              className="px-6 py-3 bg-gray-300 text-gray-700 rounded-lg hover:bg-gray-400 transition-colors"
            >
              Reset
            </button>
          </div>
        </form>
      </div>

      {/* Tax Settings */}
      <div className="p-6 bg-white rounded-lg shadow-md">
        <h2 className="text-2xl font-semibold text-gray-800 mb-4">Tax Settings</h2>
        <form onSubmit={(e) => handleSubmit(e, 'Tax')} className="space-y-4">
          <div>
            <label htmlFor="taxRate" className="block text-gray-700 font-medium">
              Tax Rate (%)
            </label>
            <input
              id="taxRate"
              type="number"
              value={taxRate}
              onChange={(e) => setTaxRate(Number(e.target.value))}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
              min={0}
              max={100}
            />
          </div>
          <div className="flex justify-between">
            <button
              type="submit"
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Save Settings
            </button>
            <button
              type="button"
              onClick={() => handleReset('Tax')}
              className="px-6 py-3 bg-gray-300 text-gray-700 rounded-lg hover:bg-gray-400 transition-colors"
            >
              Reset
            </button>
          </div>
        </form>
      </div>

      {/* Shipping Settings */}
      <div className="p-6 bg-white rounded-lg shadow-md">
        <h2 className="text-2xl font-semibold text-gray-800 mb-4">Shipping Settings</h2>
        <form onSubmit={(e) => handleSubmit(e, 'Shipping')} className="space-y-4">
          <div>
            <label htmlFor="shippingMethod" className="block text-gray-700 font-medium">
              Shipping Method
            </label>
            <select
              id="shippingMethod"
              value={shippingMethod}
              onChange={(e) => setShippingMethod(e.target.value)}
              className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
            >
              <option value="Standard">Standard</option>
              <option value="Express">Express</option>
              <option value="Overnight">Overnight</option>
            </select>
          </div>
          <div className="flex justify-between">
            <button
              type="submit"
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              Save Settings
            </button>
            <button
              type="button"
              onClick={() => handleReset('Shipping')}
              className="px-6 py-3 bg-gray-300 text-gray-700 rounded-lg hover:bg-gray-400 transition-colors"
            >
              Reset
            </button>
          </div>
        </form>
      </div>
    </section>
  );
}
